import React from "react";
import {connect} from "react-redux";
import {getFilterConfiguration, getSelectedProducer} from "../../selectors";
import {fetchFilterConfiguration, setSelectedProducer,getFilteredProductList, getFilteredProductTree, clearAttrsFilters, reloadMatchToFilter} from "eshop/modules/simfree/actions/filter";
import ProducerFilterView from "eshop/modules/simfree/components/filter/ProducerFilterView";

const mapStateToProps = state => ({
    filterConfiguration: getFilterConfiguration(state),
    selectedProducer: getSelectedProducer(state)
});

const mapDispatchToProps = (dispatch) => ({
    reloadMatchToFilter: () => dispatch(reloadMatchToFilter()),
    reloadTree:(page) => dispatch(getFilteredProductTree(page)),
    setSelectedProducer: (selectedProducer) => dispatch(setSelectedProducer(selectedProducer)),
    getFilteredList:(page) => dispatch(getFilteredProductList(page)),
    clearAttrFilters:() => dispatch(clearAttrsFilters()),

});

const ProducerFilterComponent = connect(mapStateToProps, mapDispatchToProps)(ProducerFilterView);
export default ProducerFilterComponent;